import yDrawer from '../../components/y-componnents/drawer/drawer.vue'
import uniSearchBar from '@/components/uni-search-bar/uni-search-bar.vue'
export default {
	components: {
		yDrawer,uniSearchBar

	},
	data() {
		return {
			showDrawer: false
		};
	},
	methods: {
		go(url) {
			uni.navigateTo({
				url: url
			})
		},
		scroll() {},
		onNavigationBarButtonTap() {
			this.showDrawer = true
		},
		closeDrawer() {
			this.$emit('close')
		}
	}
}
